const CourseDetails = () => {

  let { _id } = useParams()

  const [courseDetails, setCourseDetails] = useState(
    {
      "introduction": {
        "subTitle": "Mastering Android programming language and its application",
        "imageUrl": "https://images.unsplash.com/photo-1624953587687-daf255b6b80a?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1674&q=80"
      },
      "information": {
        "originalPrice": 4999,
        "discountedPrice": 499,
        "ratingStars": 4.8,
        "ratingCount": 1230,
        "enrolledStudentsCount": 10939
      },
      "extraInformation": {
        "author": "Varan",
        "language": "English",
        "timeCountInHours": 12,
        "numberOfSections": 8,
        "numberOfLectures": 120,
        "numberOfExercises": 4,
        "whatWillYouLearn": [
          "React Introduction",
          "Virtual dom",
          "Components",
          "State",
          "Props",
          "Hooks",
          "Class Components"
        ]
      },
      "_id": "61e3f7b69b3eb7f39de027cd",
      "title": "React Mastery"
    }
  )

  const getCourseDetails = async () => {
    const getCourseDetailsUrl = ApiConfig.GET_COURSE_DETAILS_INFORMATION
    const body = {
      _id: _id,
    }
    try {
      const course = await simplePostCall(getCourseDetailsUrl, body)
      if (course) {
        console.log('Course: ', course.data.course)
        setCourseDetails(course.data.course)
      }
    } catch (error) {
      console.log('Error occured: ', error.message)
    }
  }

  useEffect(() => {
    getCourseDetails()
  }, [])

  return (
    <div className='main-container'>
      <div className='course-intro-card-container'>
        <div>
          <p>
            <b>What's included</b>
          </p>
          <p className='intro-element-text'>
            <b>{courseDetails.extraInformation.numberOfLectures}</b> lectures
          </p>
          <p className='intro-element-text'>
            <b>{courseDetails.extraInformation.numberOfExercises}</b> Exercises
          </p>
          <p className='intro-element-text'>
            <b>{courseDetails.extraInformation.numberOfExercises}</b> Exercises
          </p>
          <p className='intro-element-text'>
            <b>Online</b> Accessibility
          </p>
        </div>
        <div>
          <p>Original Price: Rs {courseDetails.information.originalPrice}</p>
          <p>Discout Price: Rs {courseDetails.information.discountedPrice}</p>
          <p>77% OFF</p>
        </div>

        <div className='buy-enroll-buttons-container'>
          <div className='buy-button-view'>
            <p className='buy-button-text'>ENROLL</p>
          </div>
          {/* <div className='enroll-button-view'>
            <p className='enroll-button-text'>ENROLL FOR TRAIL</p>
          </div> */}
        </div>
      </div>

      <div>
        <p>{courseDetails.title}</p>
        <p>{courseDetails.introduction.subTitle}</p>
      </div>

      <div>
        <p>Syllabus</p>
        {
          courseDetails.extraInformation.whatWillYouLearn.map((topic, key) => {
            return (
              <div>
                <p>{topic}</p>
              </div>
            )
          })
        }
      </div>

      <div>
        <p>Authors</p>
        <div>
          <p>{courseDetails.extraInformation.author}</p>
          <p>
            I will be your trainer in this course.With no time, I will help you
            to understand the subject in easy manner. I have good experience in
            online training and recording videos. Let's get started
          </p>
        </div>
        {/* <div>
          <p>Teaching Assistants Name</p>
        </div> */}
      </div>
    </div>
  )
}

export default CourseDetails
